    // 请求淘宝接口数据
    const getTbData = async (data) => {
        let list = []
        for (let i = 0; i < data.length; i++) {
            const el = data[i];
            let GoodID = ''
            if (el.task.GoodID.indexOf("ID") != -1) {
                GoodID = el.task.GoodID.split('ID:')[1]
            } else {
                GoodID = el.task.GoodID
            }
            let urlInfo = `https://acs.m.taobao.com/h5/mtop.taobao.detail.getdetail/6.0/?data=%7B%22itemNumId%22%3A%22` + GoodID + `%22%7D&qq-pf-to=pcqq.group`
            await Ajax.jsonp({ url: urlInfo }).then(res => {
                try {
                    let goods_sum = null
                    let obj = {}
                    if (res.data.seller) {
                        let deliverInfp = JSON.parse(res.data.apiStack[0].value);
                        if (deliverInfp.item.sellCount == undefined) {
                            goods_sum = deliverInfp.item.vagueSellCount
                        } else {
                            goods_sum = deliverInfp.item.sellCount
                        }
                        obj.goods_sum = goods_sum
                        obj.good_img = res.data.item.images[0]
                        obj.good_price = JSON.parse(res.data.apiStack[0].value).price.price.priceText
                        obj.shop_name = res.data.seller.shopName
                        obj.good_title = res.data.item.title

                        if (JSON.parse(res.data.apiStack[0].value).price.priceTag) {
                            obj.good_deg = JSON.parse(res.data.apiStack[0].value).price.priceTag[0].text
                        } else {
                            obj.good_deg = ''
                        }
                        obj.good_from = JSON.parse(res.data.apiStack[0].value).delivery.from
                        // console.log(i + '0')
                    } else {
                        obj.goods_sum = ''
                        obj.good_img = ''
                        obj.good_price = ''
                        obj.shop_name = ''
                        obj.good_title = ''
                        obj.good_deg = ''
                        obj.good_from = ''
                    }
                    list.push(obj)
                } catch{
                    // debugger;
                }
                setTimeout(() => {
                    setTbData(list)
                }, 0)
            }).catch((err) => {
                // console.log(err)
                // debugger
            })
        }
    }

